* {
  box-sizing: border-box;
  padding: 0%;
  margin: 0%;
}
body {
  margin: 0;
  background: #000000;
  font-family: "Montserrat", sans-serif;
  src: url("../images/Montserrat-Regular.otf") format("opentype");
  font-size: 120%;
  color: white;
  overflow-x: hidden;
}
.cursor-class {
  mix-blend-mode: difference;
}
.background-img {
  z-index: -10;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  border: hidden;
  opacity: 20%;
}
.second-background-img {
  display: none;
}
iframe {
  border: none;
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
}
#spinner {
  visibility: visible;
  width: 10em;
  height: 10em;
  border: 2px solid #f3f3f3;
  border-top: 3px solid #1b2735;
  border-radius: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 1000000000;
  animation: spin 1s infinite linear;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
#spinner.show {
  visibility: visible;
}
#spinner.hide,
.hide-nav {
  visibility: hidden;
  display: none;
}

#black {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background-color: black;
  z-index: 100;
}
#black.show {
  visibility: visible;
}
#black.hide {
  visibility: hidden;
  display: none;
}
.nothing {
  opacity: 0%;
}

.next,
.next-phone {
  padding: 1rem;
  background-image: url(../images/next.svg);
  background-size: 100% 100%;
  background-position: center;
  background-size: cover;
  width: 2.5rem;
  height: 2.5rem;
  margin: 0;
  position: absolute;
  top: 50%;
  filter: invert(100%);
}

.mobile-new-next {
  display: none;
}

.mobile-new-back {
  display: none;
}

.inner-circle {
  border-radius: 50%;
  background-color: rgba(255, 0, 0, 0.3);
  width: 200%;
  height: 200%;
}

.back,
.back-phone {
  padding: 1rem;
  background-image: url(../images/next.svg);
  background-size: 100% 100%;
  background-position: center;
  background-size: cover;
  width: 2.5rem;
  height: 2.5rem;
  margin: 0;
  position: absolute;
  top: 50%;
  transform: scaleX(-1);
  filter: invert(100%);
}

.back-phone {
  top: 70%;
  display: none;
  margin-left: 1rem;
}

.next-phone {
  top: 70%;
  right: 0%;
  margin-right: 1rem;
  display: none;
}

.next:hover,
.back:hover {
  filter: brightness(0) saturate(100%) invert(8%) sepia(78%) saturate(7052%)
    hue-rotate(297deg) brightness(85%) contrast(105%);
}

.next-ctn {
  width: 5%;
  fill: white;
  width: 3rem;
  height: 3rem;
}
.back-ctn {
  width: 5%;
  fill: white;
  width: 3rem;
  height: 3rem;
  transform: scaleX(-1);
}
.live,
.nxt-project,
.resume-btn {
  padding: 0;
  border: none;
  background: none;
  position: absolute;
  z-index: 100000;
  bottom: 0;
  right: 0;
  text-decoration: none;
  margin: 0.1em;
  font-size: 4.5rem;
  background-color: transparent;
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}

.live:hover,
.resume-btn:hover {
  -webkit-text-stroke-width: 0;
  border-bottom-color: transparent;
  background-image: linear-gradient(90deg, #06e, #53a);
  background-size: 400% 400%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  animation: backgroundPositionShift 3s ease infinite;
  animation-name: backgroundPositionShift;
  animation-duration: 3s;
  animation-timing-function: ease;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
}

.link-btns {
  position: fixed;
  display: flex;
  justify-content: space-between;
  margin-top: 20%;
  margin-left: 2%;
  z-index: 100000000;
}

.link-btns2 {
  position: fixed;
  right: 0;
  display: flex;
  justify-content: flex-end;
  margin-top: 20%;
  margin-right: 2%;
  z-index: 100000000;
}

.live,
.nxt-project {
  padding: 1rem;
  font-size: 2rem;
  position: relative;
}

.nxt-project {
  padding: 1rem;
  background-image: url(../images/next.svg);
  background-size: 100% 100%;
  filter: invert(100%);
}

.nxt-project:hover {
  filter: brightness(0) saturate(100%) invert(8%) sepia(78%) saturate(7052%)
    hue-rotate(297deg) brightness(85%) contrast(105%);
}

.but-back {
  transform: scaleX(-1);
}

@keyframes backgroundPositionShift {
  0% {
    background-position: 0%;
  }
  40% {
    background-position: 100%;
  }
  100% {
    background-position: 0%;
  }
}

#circle {
  width: 80px;
  height: 80px;
  border: 1px solid #3c2946;
  position: fixed;
  border-radius: 50%;
}
li,
a {
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  color: #edf0f1;
  text-decoration: none;
  font-weight: normal;
  letter-spacing: -0.04em;
}
li,
a:hover {
  /* color:purple; */
}
.timeLocation {
  color: white;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}
.logo {
  cursor: pointer;
  padding: 5px 5px;
  width: 4rem;
}

.middle {
  padding-left: 10%;
  padding-right: 10%;
  display: flex;
  justify-content: space-evenly;
}
.container {
  width: 80%;
  height: 100vh;
  cursor: pointer;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 10%;
  padding-top: 4rem;
}

.inside {
  position: absolute;
  z-index: 100000000;
  bottom: 0%;
  left: 50;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 10%;
  background-color: red;
  filter: blur(20px);
  opacity: 50%;
}

.nav-links {
  list-style: none;
}
.nav-links li {
  display: inline-block;
  padding: 0px 20px;
}
.nav-links li a {
  transition: all 0.3s ease 0s;
}
a::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: purple;
  transition: width 0.3s;
}

a:hover::after {
  width: 100%;
  transition: width 0.3s;
}

.social a:hover::after {
  width: 0%;
}

h1 {
  font-size: 2.5rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.5rem;
}
.ProjectName,
.right h1 {
  -webkit-text-stroke-width: 0.125rem;
  -webkit-text-fill-color: #eee;
  -webkit-text-stroke-color: #eee;
  font-size: 7rem;
  letter-spacing: 0.04em;
  padding: 1rem;
  /* padding-bottom:4rem; */
}
.right h1 {
  padding-bottom: 1rem;
}
.Subtitle,
.Main-Category,
.Description,
.Role,
.img-handler img,
.year,
.left,
.right,
.social,
.info-cont,
.defaults,
.current,
.vid {
  padding: 1rem;
}

.namedit {
  padding-bottom: 1rem;
}

.current li {
  display: inline-block;
  padding: 0px 20px;
  font-size: 12px;
}

.current a {
  color: #a8a8a8;
  font-size: 1rem;
}

.defaults {
  display: flex;
  text-decoration: none;
  font-size: 1rem;
  align-items: center;
  margin-bottom: 2rem;
}

.yandr {
  font-size: 1rem;
}

.Subtitle {
  font-size: 1rem;
}
.Description {
  /* font-weight: lighter; */
  /* line-height: 150%; */
  font-size: 1rem;
  width: 40%;
  margin-bottom: 2rem;
}
.case-study,
.case-study-info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.case-study {
  padding: 10px 10%;
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.case-study-info {
  border-bottom: 1px solid #fff;
}
.descriptors {
  padding-top: 2%;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}
.arrowtxt {
  position: absolute;
  bottom: 7%;
  left: 50%;
  font-weight: lighter;
  font-size: 3rem;
  animation: blinkingText 1.2s infinite;
  visibility: hidden;
}
.arrowtxt.show {
  visibility: visible;
}
.arrowtxt.hide {
  visibility: hidden;
}
@keyframes blinkingText {
  0% {
    color: #fffef2;
  }
  49% {
    color: #fffef2;
  }
  60% {
    color: transparent;
  }
  99% {
    color: transparent;
  }
  100% {
    color: #fffef2;
  }
}

.img-handler {
  display: flex;
  flex-direction: column;
  width: 90%;
}

.img-handler img {
  max-height: 40vh;
}

.extracase {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.extra-info {
  font-size: 1rem;
  padding: 1rem;
  width: 40%;
  margin-bottom: 2rem;
}
.tech-stack {
  display: flex;
}
.tech-stack p {
  color: white;
  font-size: 1rem;
}
.fade-in {
  animation: animationFrames ease 1s;
  animation-iteration-count: 1;
  transform-origin: 50% 50%;
  animation-fill-mode: forwards; /*when the spec is finished*/
  -webkit-animation: animationFrames ease 1s;
  -webkit-animation-iteration-count: 1;
  -webkit-transform-origin: 50% 50%;
  -webkit-animation-fill-mode: forwards; /*Chrome 16+, Safari 4+*/
  -moz-animation: animationFrames ease 1s;
  -moz-animation-iteration-count: 1;
  -moz-transform-origin: 50% 50%;
  -moz-animation-fill-mode: forwards; /*FF 5+*/
  -o-animation: animationFrames ease 1s;
  -o-animation-iteration-count: 1;
  -o-transform-origin: 50% 50%;
  -o-animation-fill-mode: forwards; /*Not implemented yet*/
  -ms-animation: animationFrames ease 1s;
  -ms-animation-iteration-count: 1;
  -ms-transform-origin: 50% 50%;
  -ms-animation-fill-mode: forwards; /*IE 10+*/
}

@keyframes animationFrames {
  0% {
    opacity: 0;
    transform: translate(0px, -25px);
  }
  100% {
    opacity: 1;
    transform: translate(0px, 0px);
  }
}

@-moz-keyframes animationFrames {
  0% {
    opacity: 0;
    -moz-transform: translate(0px, -25px);
  }
  100% {
    opacity: 1;
    -moz-transform: translate(0px, 0px);
  }
}

@-webkit-keyframes animationFrames {
  0% {
    opacity: 0;
    -webkit-transform: translate(0px, -25px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0px, 0px);
  }
}

@-o-keyframes animationFrames {
  0% {
    opacity: 0;
    -o-transform: translate(0px, -25px);
  }
  100% {
    opacity: 1;
    -o-transform: translate(0px, 0px);
  }
}

@-ms-keyframes animationFrames {
  0% {
    opacity: 0;
    -ms-transform: translate(0px, -25px);
  }
  100% {
    opacity: 1;
    -ms-transform: translate(0px, 0px);
  }
}

