@media (max-width: 30em) {
  body {
    overflow: visible;
  }
  header {
    justify-content: space-between;
    padding: 2%;
  }
  .nav-links li a {
    font-size: 80%;
  }
  .link-btns {
    width: 0;
    height: 0;
    margin-top: 0;
    margin-left: 0%;
    margin-bottom: 10%;
    position: fixed;
    opacity: 50%;
    z-index: 10000000;
  }
  .link-btns2 {
    width: 0;
    height: 0;
    margin-top: 0;
    margin-right: 0%;
    margin-bottom: 10%;

    position: fixed;
    opacity: 50%;
    z-index: 10000000;
  }
  .right-cont {
    flex-basis: auto;
    justify-content: center;
  }
  .background-img {
    opacity: 30%;
    width: 100vw;
    height: 50vh;
  }
  .second-background-img {
    opacity: 30%;
    z-index: -12;
    display: inline;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 50vh;
  }
  .next {
    top: 85%;
  }
  .back {
    top: 85%;
  }
  .container {
    width: 100%;
